<template>
  <div>
    <b-breadcrumb :items="menus"/>
    <b-container fluid>
      <div style="padding-bottom:10px;">
        <b-button size="sm" class="mr-2" variant="outline-theme" @click="getTableData">
          手动刷新所有测序文件当前状态
        </b-button>
      </div>
      <b-table bordered responsive hover :items="queueLists" :fields="fields" head-variant='light' show-empty :empty-text="tableEmptyText">
        <template slot="vip" slot-scope="row">
          <span v-show="row.item.vip==='1'" style="color:red">VIP</span>
        </template>
        <template slot="seq_status" slot-scope="row">
          <span v-show="row.item.seq_status=='1'">等待分析</span>
          <span v-show="row.item.seq_status=='2'" style="color:#ffc107">分析中</span>
          <span v-show="row.item.seq_status=='3'" style="color:#41babc">分析成功</span>
          <span v-show="row.item.seq_status=='4'" style="color:#f00">分析失败</span>
        </template>
        <template slot="operate" slot-scope="row">
          <b-button size="sm" class="mr-2" variant="outline-theme" @click="removeQueue(row)" v-show="row.item.seq_status==='1'">
            移除队列
          </b-button>
        </template>
      </b-table>
      <div class="paginationCon">
        <b-pagination size="md" :number-of-pages="10" :total-rows="total" v-model="currentPage" @input="changePage" :per-page="10"></b-pagination>
      </div>
    </b-container>
  </div>
</template>
<script>
  export default{
    components:{},
    mounted(){
      
      this.getTableData()
      this.setTime()
    },
    beforeDestroy(){
      clearInterval(this.timer)
    },
    data () {
      return {
        menus: [{
          text: '测序队列管理',
          href: '#'
        }],
        timer:null,
        fields: {
          order_id:{
            label:'申请单号'
          },
          name:{
            label:'受检者姓名'
          },
          product:{
            label:'产品名'
          },
          vip:{
            label:'VIP'
          },
          seq_operator:{
            label:'测序操作人'
          },
          seq_status:{
            label:'测序分析状态'
          },
          seq_starttime:{
            label:'开始分析时间'
          },
          seq_endtime:{
            label:'结束分析时间'
          },
          operate:{
            label:'操作'
          }
        },
        tableEmptyText:'正在请求数据...',
        queueLists: [],
        currentPage:1,
        total:0
      }
    },
    methods:{
      getTableData(){
        this.$Loading.start()
        this.tableEmptyText = "正在请求数据..."
        this.$http.post("/api/Analysis/list",qs.stringify({page:this.currentPage})).then(response=>{
        //   // console.log(response.data)
          this.queueLists=response.data.data
          Number(response.data.total)===0?this.tableEmptyText = "[空]":null
          this.total = Number(response.data.total)
          this.$Loading.finish()
          this.setTime()
        })
      },
      removeQueue(row){
        // console.log(row.item)
        this.$http.post("/api/Analysis/removeTask",qs.stringify({order_id:row.item.order_id})).then(response=>{
          this.getTableData()
        })
      },
      setTime(){
        // 先先清除定时器
        clearInterval(this.timer)
        this.timer = setInterval(() => {
          this.$Message.loading('正在刷新队列...')
          this.getTableData()
        }, 40000)
      },
      changePage(){
        this.getTableData()
      }
    }

  }
</script>
<style lang='less' scoped>
@import '../../../less/init.less';
#plasm_use_unit{
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
#plasm_unit{
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
</style>


